import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Dia from './views/comp/dialog.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import PersonCode from './views/bmgl/bm_person.vue'
import EquipCode from './views/bmgl/bm_equip.vue'
import LicenseCode from './views/bmgl/bm_license.vue'
import ProgressCode from './views/bmgl/bm_progress.vue'
import AreaCode from './views/bmgl/bm_area.vue'
import ProviderCode from './views/bmgl/bm_provider.vue'
import Page7 from './views/nav2/myELeTableTry.vue'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'

let flag=1;
let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
     {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-home',
        leaf: true,//
        children: [
            { path: '/main', component: Main, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/main', component: Main, name: '', hidden: true },
            { path: '/table', component: Table, name: 'Table' },
            { path: '/form', component: Form, name: 'Form' },
            { path: '/user', component: user, name: '' },
            { path: '/dialog', component: Dia, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/page4', component: Page4, name: '4' },
            { path: '/page5', component: Page5, name: '5' },
            { path: '/page7', component: Page7, name: 'table' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            { path: '/page6', component: Page6, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/bm_person', component: PersonCode,name: ''},
            { path: '/bm_equip', component: EquipCode, name: ''},
            { path: '/bm_license', component: LicenseCode, name: '' },
            { path: '/bm_progress', component: ProgressCode, name: ''},
            { path: '/bm_area', component: AreaCode, name: ''},
            { path: '/bm_provider', component: ProviderCode, name: ''}
        ]
    },
//  {
//      path: '/',
//      component: Home,
//      name: 'Charts',
//      iconCls: 'fa fa-bar-chart',
//      children: [
//          { path: '/echarts', component: echarts, name: 'echarts' }
//      ]
//  },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


