<template>
    <el-dialog :data='showData' :title='showData.title' :rules='showData.rules' :visible.sync='showData.dialogVisible' class='my-form-dialog' :before-close='cancel'>
    	<el-form :inline='true' label-width='100px'>
    		<el-row class='my-form-item'>
					<el-col :span='24'>
						<el-form-item label="" class='is-required'>
							<el-input></el-input>
						</el-form-item>
					</el-col>
    		</el-row>
    		<el-row class='my-form-item'>
    			<el-col :span='8'>
    				<el-form-item label="" class='is-required'>
						<my-select :showData='showData.provinces'></my-select>
					</el-form-item>
    			</el-col>
    			<el-col :span='8'>
    				<el-form-item label="" class='is-required'>
						<my-select :showData='showData.types'></my-select>
					</el-form-item>
    			</el-col>
    			<el-col :span='8'>
    				<el-form-item label="" class='is-required'>
						<my-select :showData='showData.cates'></my-select>
					</el-form-item>
    			</el-col>
    		</el-row>
    		<el-row class='my-form-item'>
					<el-col :span='24'>
						<el-form-item label="">
							<el-input type='textarea' resize='none'></el-input>
						</el-form-item>
					</el-col>
    		</el-row>
    		<el-row class='my-form-item'>
    			<el-col :span='8'>
    				<el-form-item class='my-form-item-check'>
							<el-checkbox ></el-checkbox>
						</el-form-item>
    			</el-col>
    			<el-col :span='8'>
    				<el-form-item label=''  class='my-form-item-check my-form-item-check_input'>
							<el-checkbox ></el-checkbox>
							<el-date-picker type="date" placeholder="" v-model="showData.date1"></el-date-picker>
						</el-form-item>
    			</el-col>
    			<el-col :span='8'>
    				<el-form-item  label='' class='my-form-item-check my-form-item-check_input'>
							<el-checkbox ></el-checkbox>
							<my-select :showData='showData.fj1'></my-select>
						</el-form-item>
    			</el-col>
    		</el-row>
    		<el-row class='my-form-item'>
    			<el-col :span='8'>
    				<el-form-item label=''  class='my-form-item-check my-form-item-check_input'>
							<el-checkbox ></el-checkbox>
							<my-select :showData='showData.licensetypes'></my-select>
						</el-form-item>
    			</el-col>
    			<el-col :span='8'>
    				<el-form-item label=''  class='my-form-item-check my-form-item-check_input'>
							<el-checkbox ></el-checkbox>
							<el-date-picker type="date" placeholder="" v-model="showData.date2"></el-date-picker>
						</el-form-item>
    			</el-col>
    			<el-col :span='8'>
    				<el-form-item label='' class='my-form-item-check my-form-item-check_input'>
							<el-checkbox ></el-checkbox>
							<my-select :showData='showData.fj2'></my-select>
						</el-form-item>
    			</el-col>
    		</el-row>
    	</el-form>
    	 <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="upload"> </el-button>
        <el-button type="primary" @click="sure"> </el-button>
        <el-button @click="cancel"> </el-button>
      </span>
    </el-dialog>
</template>

<script>

	export default{
		props:{
			showData:{
				type:Object,
				default(){
					return {
						title:'',
						dialogVisible:false,
						date1:'',
						date2:'',
						cancelFlag:false,
						provinces:{}, //myselect
						types:{},
						cates:{},
						fj1:{},
						licensetypes:{},
						fj2:{},
						rules:{}
					}
				}
			}
		},
		methods:{
			closeWin(){
				this.showData.dialogVisible=false;
			},
			upload(){
				this.$message.error('');
			},
			sure(){
				this.$emit('onSure',this.closeWin);
			},
			cancel(){
				if(!this.showData.cancelFlag)return this.closeWin(); 
				this.$emit('onCancel',this.closeWin);
			}
		},
		mounted(){
//			this.rules={showDate
//				
//			};
		}
	}
</script>

<style>
</style>


