<template>
	<el-form ref="zzform" :model="form" label-width="80px" :rules='rules' @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
		<el-form-item label="">
			<el-input v-model="form.name"></el-input>
		</el-form-item>
		<el-form-item label="">
			<el-select v-model="form.region" placeholder="">
				<el-option label="" value="shanghai"></el-option>
				<el-option label="" value="beijing"></el-option>
			</el-select>
		</el-form-item>
		<el-form-item label="">
			<el-col :span="11">
				<el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
			</el-col>
			<el-col class="line" :span="2">-</el-col>
			<el-col :span="11">
				<el-time-picker type="fixed-time" placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
			</el-col>
		</el-form-item>
		<el-form-item label="">
			<el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
		</el-form-item>
		<el-form-item label="" prop='type'>
			<el-checkbox-group v-model="form.type">
				<el-checkbox label="1" name="type">/</el-checkbox>
				<el-checkbox label="2" name="type"></el-checkbox>
				<el-checkbox label="3" name="type"></el-checkbox>
				<el-checkbox label="4" name="type"></el-checkbox>
			</el-checkbox-group>
		</el-form-item>
		<el-form-item label=''>
			<my-checkgroup :checkdata="checkdata"></my-checkgroup>
		</el-form-item>
		<el-form-item label='' prop='fruit'>
			<my-checkgroup :checkdata='fruits'></my-checkgroup>
		</el-form-item>
		<el-form-item label=''>
			<my-checkgroup :checkdata='order'></my-checkgroup>
		</el-form-item>
		<el-form-item label="">
			<el-radio-group v-model="form.resource">
				<el-radio label="1"></el-radio>
				<el-radio label="2"></el-radio>
			</el-radio-group>
		</el-form-item>
		<el-form-item label='' prop='sex'>
			<my-radio :radiodata='sexs'></my-radio>
		</el-form-item>
		<el-form-item label="">
			<el-input type="textarea" v-model="form.desc"></el-input>
		</el-form-item>
		<el-form-item>
			<el-button type="primary" @click='createNew'></el-button>
			<el-button @click.native.prevent></el-button>
			<el-button @click="resetForm('zzform')"></el-button>
		</el-form-item>
		<el-form-item  prop='zz' label='' class='is-required'>
			<my-select :showData='selectData'>
			</my-select>
		</el-form-item>
		<el-form-item prop='zz3' label=''>
			<my-select-multiple :showData='selectDataM'>
			</my-select-multiple>
		</el-form-item>
		<el-form-item prop='zz4' label=''>
			<el-input type='number' v-model='num'></el-input>
		</el-form-item>
	</el-form>
</template>

<script>
	export default {
		data() {
			let $this=this;
			var checkAge = (rule, value, callback) => {
		        if (!$this.selectData.selected) {
		          return callback(new Error(''));
		        }
		      };
			return {
				num:'',
				rules:{
					type:[{type:'array',required:true,message:'',trigger:'change'}],
					zz:[{validator:checkAge,trigger:'change'}]       //formitemclass='is-required';
				},
				selectData:{
					selected:1,
					hasEmpty:true,
					options:[{text:'zz',value:1},{text:'asdgas',value:2,disabled:true}]
				},
				selectDataM:{
					selected:[],
					options:[{text:'zz',value:1},{text:'asdgas',value:2,disabled:true},{text:'',value:3}]
				},
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					sex:'',
					resource: '',
					desc: ''
				},
				sexs:{
					checked:'',
					data:[{text:'',id:'1'},{text:'',id:'2'}],
					textfield:'text',
					valuefield:'id'
				},
				order:{
					data:[{id:'1'}],
					valuefield:'id',
					checkeddata:[]
				},
				checkdata:{
					name:'food',
					data:[{name:'',uid:'123',disabled:true},{name:'',uid:'1234'}],
					valuefield:'uid',
					textfield:'name',
					checkeddata:[]
				},
				fruits:{
//					hasAllselect:true,
//					checkAll:false,
					name:'fruit',
					data:[{text:'',id:1},{text:'',id:2},{text:'',id:3}],
					textfield:'text',
					valuefield:'id',
					checkeddata:[]
				}
			}
		},
		methods: {
			onSubmit() {
				console.log('submit!');
				console.log(this.form);

			},
			createNew(){
//				console.log(this.form);
				console.log(this.num);
				console.log(this.selectData);
				console.log(this.selectDataM);
				
			},
			resetForm(formName) {
		        this.$refs[formName].resetFields();
		    }
		}
	}

</script>


